






















































































































































import { Component, Vue } from "vue-property-decorator";
import api from "@/microApps/openPlatform/api";

@Component
export default class ApplicationQualificationAudit extends Vue {
  tableForm = {
    developerName: "",
    authStatus: ""
  };
  loading = false;
  tableData = [];
  total = 0;
  pageNum = 1;
  pageSize = 10;

  // 图片预览
  dialogImageVisible: boolean = false;
  dialogImageUrl: string = "";

  created() {
    this.getTableData();
  }

  search() {
    this.pageNum = 1;
    this.getTableData();
  }

  getTableData() {
    this.loading = true;
    this.$http
      .post(api.getAllQualificationList, {
        query: this.tableForm,
        current: this.pageNum,
        size: this.pageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.tableData = res.data.data.records;
          for (let i in this.tableData) {
            this.$set(this.tableData[i], "isShow", false);
          }
          this.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getTableData();
  }
  handleSizeChange(val: number) {
    this.pageSize = val;
    this.getTableData();
  }

  // 通过
  handleAgree(id: string) {
    this.$confirm("确认通过？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "info"
    })
      .then(() => {
        this.$http.get(api.agreeQualificationApply + "/" + id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "操作成功!"
              });
              //重新获取列表
              this.getTableData();
            } else {
              this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  // 拒绝
  handleRefuse(id: string) {
    this.$prompt("拒绝原因", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      inputValidator: value => {
        if (value) {
          return true;
        } else {
          return false;
        }
      },
      inputErrorMessage: "请输入拒绝原因"
    })
      .then((obj: any) => {
        this.$http
          .post(api.refusequalificationApply + "/" + id, null, {
            params: {
              rejectReason: obj.value
            }
          })
          .then((res: any) => {
            if (res.status) {
              if (res.data.status) {
                this.$message({
                  type: "success",
                  message: "操作成功!"
                });
                //重新获取列表
                this.getTableData();
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.message,
                  type: "error"
                });
              }
            }
          });
      })
      .catch(() => {});
  }

  // 图片预览
  handlePreview(url: string) {
    this.dialogImageUrl = url;
    this.dialogImageVisible = true;
  }
}
